.new-product--container {
  margin: 45px 108px 39px 110px;
}

.new-product {

  &__left-side {
    width: 355px;
  }

  &__form {
    &--first-input {
      margin-bottom: 6px;
    }
  }

  &__input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background: #FFFFFF;
    border-radius: 8px;
    height: 40px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    margin-bottom: 12px;
  }

  &__top-actions {
    display: flex;
    justify-content: flex-end;
    gap: 75px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    &--text {
      &-active {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        position: relative;

        &::after {
          display: block;
          content: '';

          position: absolute;
          bottom: -11px;

          width: 100%;
          height: 1px;

          border-radius: 2px;
          background-color: #5A8FFF;
        }
      }
    }
  }

  &__title {
    display: flex;
    align-items: center;
    gap: 28px;
    margin-bottom: 16px;

    &-main {
      margin: 0;
    }
  }

  &__product-id {
    &--text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      margin-right: 4px;
      color: #919090;
    }

    &--id {
      color: #000;
      font-weight: 500;
      margin-right: 6px;
    }
  }

  &__add-photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #FFFFFF;
    border-radius: 8px;
    width: 100%;
    height: 326px;
    margin-bottom: 15px;

    &--text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 300;
      font-size: 12px;
      line-height: 15px;

      /* BLUE GRADIENT */

      background: linear-gradient(180deg, #5A8FFF 0%, #003094 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;

      #file {
        display: none;
      }
    }
  }
}

.labelIcon {
  margin-right: 8px;
}